












import Vue from 'vue';
import EditableText from './editable-text.vue';

export default Vue.extend({
  name: 'TestQuestion',
  components: {
    EditableText,
  },
  props: {
    edit: Boolean,
    value: String,
    index: Number,
  },
  computed: {
    questionValue: {
      get() {
        return (this as {value: string}).value;
      },
      set(val) {
        (this as Vue).$emit('change', val);
      },
    },
    num() {
      return `#${this.index < 9 ? '0' : ''}${(this.index as number) + 1}`;
    },
  },
});
